
.left-panel {
    width: 500px;
    height: 100%;
}

.bottom-left-panel, .top-left-panel {
    width: 100%;
    overflow: auto;
}

.top-left-panel {
    height: 50%;
    padding: 5px;
    padding-top: 0px;
    border-bottom: 3px solid hsl(213, 13%, 16%);
}

.bottom-left-panel {
    height: 50%;
    overflow: scroll;
    overflow-x: scroll;
    padding: 5px;
}

.sample-row {
    padding: 5px;
}

.alternate-color {
    background-color: hsl(0, 0%, 93%);
}

.inspected-sample {
    background-color: hsl(53, 100%, 89%);
}

.sample-ul {
    margin: 0px;
}

.sample-ul > * {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.sample-index, .heading-index {
    width: 60px;
}

.sample-first-column, .heading-first-column {
    width: 210px;
}

.sample-label, .heading-label {
    width: 100px;
}

.inspect-button {
    padding: 0px 10px;
}

.label-select {
    width: 100px;
}

.list-feature {
    font-weight: bold;
}

.list-value, .commit-link {
    color: hsl(0, 0%, 20%);
}
