* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji;
    font-size: 14px;
}

.page-container {
    display: flex;
    flex-direction: column;
    height: 99.5vh;
}

.top-nav {
    height: 40px;
    padding:5px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: hsl(213, 13%, 16%);
    color: white;
    font-weight: bold;
;
}

.top-left-nav {
    display: flex;
    flex-direction: row;
    width: 500px;
}

.bottom-container {
    height: calc(99.5vh - 36px);
    display: flex;
}

.right-panel {
    flex: 1;
    height: 99.5%;
    /* background-color: lightblue; */
    overflow: scroll;

    display: flex;
}

.iframe {
    width: 100%;
    height: 100%;
    border: 0;
}

.heading-ul, .sample-ul {
    display: flex;
    flex-direction: row;
    padding: 0px;
    margin: 5px;
}

.heading-ul > li, .sample-ul > li {
    list-style-type: none;
    margin-right: 5px;
}

.top-right-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.upload-button, .download-button {
    height: 30px;
    width: 100px;
    border-radius: 5px;
    border-color: none;
}

.download-button {
    background-color: hsl(212, 53%, 50%);
    border: none;
    color: white;
    font-weight: bold;
}

.download-button:hover {
    background-color: hsl(212, 53%, 40%);
    cursor: pointer;
}

.input-file {
    width: 200px;
    background-color: hsl(0, 59%, 54%);
    margin-right: 100px;
    display: flex;
    padding: 4px;
    border-radius: 5px;
    color: white;
}

.input-file:hover {
    background-color: hsl(0, 59%, 44%);
    cursor: pointer;
}

.object-embed {
    width: 500px;
    height: 500px;
}

code, .line-number {
    white-space: pre;
    font-family: ui-monospace,SFMono-Regular,SF Mono,Menlo,Consolas,Liberation Mono,monospace;
    font-size: 12px;
}

.highlight-line {
    background-color: hsl(53, 100%, 89%);
}

.line-numbers {
    margin-right: 10px;
    text-align: right;
}
